<template>
  <div class="hello">
    <nav class="navbar navbar-expand-lg">
      <a class="navbar-brand orange-light" href="/">MOVIER</a>
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <img
          src="https://www.clipartmax.com/png/full/36-365828_navbar-toggle-icon-menu-hamburger-png-white.png"
          alt="hamburguer menu"
          style="width: 30px; heigth: 30px"
        />
      </button>

      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">
          <li class="nav-item active">
            <button @click="logInAsGuest">Entrar como convidado</button>
          </li>
          <li class="nav-item active">
            <button @click="logInWithTMDB">Entrar com TMDB</button>
          </li>
          <li class="nav-item active">
            <button @click="authorizeTMDB">Autorizar Token</button>
          </li>
          <li class="nav-item active">
            <button @click="refreshToken">Token Inválido?</button>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>

<script>
import domain from '@/helpers/helpers'

export default {
  name: 'LoginBar',
  methods: {
    refreshToken () {
      localStorage.clear()
      this.$router.go({ name: 'login' })
    },
    logInAsGuest () {
      this.$store.dispatch('createGuest')
    },
    logInWithTMDB () {
      this.$store.dispatch('logInWithTMDB')
    },
    authorizeTMDB () {
      window.location.href = `https://www.themoviedb.org/authenticate/${this.$store.state.requestToken}?redirect_to=${domain}`
    }
  }
}
</script>

